import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import "../styles/global.css";
import style from "../styles/login.module.css";
import { hot } from "react-hot-loader/root";
import APIService from "../servers/APIService";

function Password() {
    const [pwdType, setPwdType] = useState("text");
    const [confirmpwdType, setConfirmPwdType] = useState("text");

    useEffect(() => {}, []);

    const loginBtnClicked = (event) => {
        event.preventDefault();

        // APIService.login().then((res) => {
        //     if (res && res.success === 1) {
        //         location.href = "./index.html";
        //     }
        // });
    };
    return (
        <>
            <div style={{ fontSize: 16 }} className={style.password_title}>
                Welcomeï¼ŒPlease enter your infomation
            </div>
            <div style={{ fontSize: 12, color: "#A5AEB8" }} className={style.password_title}>
                <span className={style.pir_rg}>AIBANK ID</span>
                <span style={{ marginLeft: 50 }}>AiBANK10000</span>
            </div>

            <form className="text-left" onSubmit={loginBtnClicked}>
                <div className="form">
                    <div id="username-field" className="field-wrapper input">
                        <label className={style.pir_rg} htmlFor="username">
                            SET YOUR PASSWORD
                        </label>
                        <i style={{ top: 60 }} data-feather="lock"></i>

                        <input
                            id="username"
                            name="username"
                            type={pwdType}
                            className="form-control"
                            placeholder="Password"
                            required
                        />
                        <div
                            onClick={() => {
                                if (pwdType === "password") {
                                    setPwdType("text");
                                } else {
                                    setPwdType("password");
                                }
                            }}
                        >
                            <i style={{ top: 60 }} data-feather="eye" />
                        </div>
                    </div>

                    <div id="password-field" className="field-wrapper input mb-2">
                        <div className="d-flex justify-content-between">
                            <label className={style.pir_rg} htmlFor="password">
                                ENTER YOUR PASSWORD AGAIN
                            </label>
                        </div>

                        <i style={{ top: 60 }} data-feather="lock" />

                        <input
                            id="password"
                            name="password"
                            type={confirmpwdType}
                            className="form-control"
                            placeholder="Password"
                            required
                        />
                        <div
                            onClick={() => {
                                if (confirmpwdType === "password") {
                                    setConfirmPwdType("text");
                                } else {
                                    setConfirmPwdType("password");
                                }
                            }}
                        >
                            <i style={{ top: 60 }} data-feather="eye" />
                        </div>
                    </div>
                    <div className="d-sm-flex justify-content-between">
                        <div className="field-wrapper">
                            <input
                                style={{ height: 55, marginBottom: 30 }}
                                type="submit"
                                className={`btn btn-primary ${style.pir_rg}`}
                                value="Log In"
                            />
                        </div>
                    </div>
                </div>
            </form>
        </>
    );
}
const WithHotReload = process.env.NODE_ENV === "production" ? Password : hot(Password);

ReactDOM.render(<WithHotReload />, document.getElementById("root"));
